var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-card',[_c('v-card-title',{staticClass:"title"},[_c('strong',[_vm._v(" Цель и задачи проекта ")])]),_c('v-divider',{staticClass:"mx-3"}),_c('v-card-text',[_c('div',{staticClass:"text"},[_c('p',[_vm._v("\n        Проект направлен на изучение процессов ускорения и распространения частиц от\n        области энерговыделения в солнечных вспышках, и, прежде всего, на определение\n        источников потоков электронов во внешнюю корону. Планируется объединить\n        инструментальные возможности, а также знания и опыт двух коллективов России и\n        Великобритании - Радиоастрофизического отдела Института солнечно-земной\n        физики СО РАН, и группы физики Солнца Мюллардовской Лаборатории Космических\n        исследований Университетского Колледжа Лондона.\n      ")]),_c('p',[_vm._v("\n        Метровые радиовсплески III типа представляют собой трассеры распространения\n        ускоренных в солнечной атмосфере электронов в межпланетном пространстве и\n        являются важным проявлением геоэффективности солнечных вспышек. Основой\n        планируемого исследования станут оригинальные наблюдения, проведенные\n        одновременно двумя радиоинтерферометрами последнего поколения (Сибирского\n        радиогелиографа 4-8 ГГц (СРГ) и Low-Frequency Array (LOFAR), работающего в\n        диапазоне от 10-250 МГц). В ходе проекта будет выполнен сравнительный анализ\n        пространственно-временной структуры радиовсплесков III типа и их источников,\n        наблюдаемых в микроволновом диапазоне. Данные наблюдений микроволновых\n        источников позволят определить динамику ускорения электронов и локализовать\n        области ускорения, а метровые всплески III типа - проследить за процессами выхода\n        ускоренных электронов во внешние слои короны и верифицировать существующие\n        модели распределений температуры и плотности до высот порядка радиуса Солнца.\n        